/**
 *  forms mixin module
 *  @module base/mixin/forms
 *  @author coder
 */
import {
  fetchForms,
  getForms,
  addForms,
  updateForms,
  removeForms,
  batchForms
} from '@/base/api/forms'

// mixin
export default {

  /**
   * forms
   * @member forms
   * @property {Array} [list=[]] forms
   * @property {number} [page=null] forms
   * @property {number} [limit=null] forms
   * @property {number} [total=0] forms
   * @property {object} [model=null] forms
   */
  data() {
    return {
      forms: {
        list: [],
        page: null,
        limit: null,
        total: 0,
        model: null
      }
    }
  },
  methods: {

    /**
     * forms
     * @function fetchForms
     * @param {object} data data
     * @returns {Promise} Promise
     */
    fetchForms(data) {
      return fetchForms(data).then(res => {
        this.forms.list = res.list || []
        this.forms.page = Number.parseInt(res.page) || 0
        this.forms.limit = Number.parseInt(res.limit) || 0
        this.forms.total = Number.parseInt(res.total) || 0
        return res
      })
    },
    /**
     * forms
     * @function getForms
     * @param {string|number} id URLid
     * @param {object} data data
     * @returns {Promise} Promise
     */
    getForms(id, data) {
      return getForms(id, data).then(res => {
        this.forms.model = Object.assign({}, data, res || {})
        return res
      })
    },
    /**
     * forms
     * @function addForms
     * @param {object} data data
     * @param {number} [index] 
     * @returns {Promise} Promise
     */
    addForms(data, index) {
      return addForms(data, index).then(res => {
        this.forms.model = Object.assign({}, data, res || {})
        if (index !== undefined) {
          this.forms.list.splice(index, 0, this.forms.model)
        } else {
          this.forms.list.push(this.forms.model);
        }
        this.forms.total += 1;
        return res
      })
    },
    /**
     * forms
     * @function updateForms
     * @param {object} data data
     * @param {number} [index] index
     * @returns {Promise} Promise
     */
    updateForms(data, index) {
      return updateForms(data, index).then(res => {
        this.forms.model = Object.assign({}, data, res || {})
        if (index !== undefined) {
          this.forms.list.splice(index, 1, this.forms.model)
        }
        return res
      })
    },
    /**
     * forms
     * @function removeForms
     * @param {string|number} id URLid
     * @param {object} data data
     * @param {number} [index] index
     * @returns {Promise} Promise
     */
    removeForms(id, data, index) {
      return removeForms(id, data).then(res => {
        if (index !== undefined) {
          this.forms.list.splice(index, 1)
        }
        this.forms.total -= 1;
        return res
      })
    },
    /**
     * forms
     * @function batchForms
     * @param {object} data data
     * @param {number[]} [indexes] indexes
     * @returns {Promise} Promise
     */
    batchForms(data, indexes = []) {
      return batchForms(data).then(res => {
        if (indexes.length > 0) {
          this.forms.list = this.forms.list.filter((item, index) => !indexes.includes(index))
        }
        this.forms.total -= indexes.length;
        return res
      })
    }
  }
}
