const list = [
  {
    type: 'switch',
    prop: 'required',
    label: ''
  },
  {
    type: 'input',
    prop: 'message',
    label: '',
    rules: [
      {required: true}
    ]
  },
  {
    type: 'radio',
    prop: 'trigger',
    label: '',
    value: '',
    props: {
      button: true
    },
    options: [
      'change',
      'blur'
    ]
  },
  {
    type: 'select',
    prop: 'type',
    label: '',
    options: [
      'string',
      'number',
      'boolean',
      'integer',
      'float',
      'array',
      'date',
      'url',
      'email'
    ]
  },
  {
    type: 'input',
    prop: 'pattern',
    label: ''
  },
  {
    type: 'number',
    prop: 'min',
    label: ''
  },
  {
    type: 'number',
    prop: 'max',
    label: ''
  },
  {
    type: 'number',
    prop: 'len',
    label: ''
  }
]


export function getModel(list = []) {
  let m = {}
  list.forEach(item => {
    if (typeof item.value !== 'undefined') {
      m[item.prop] = item.value
    }
  })
  return m
}

export default {
  fields: list,
  model: getModel(list)
}
